@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
html {
  font-size: 62.5%;
  box-sizing: border-box;
}
body,
#root {
  height: 100vh;
  margin: 0;
}
body,
input,
select,
button,
textarea {
  font-family: Arial, Helvetica, sans-serif !important;
  font-size: 1.6rem;
}
.button.primary {
  background-color: #f94856;
}
.grid-container {
  display: grid;
  grid-template-areas:
    'header'
    'main'
    'footer';
  grid-template-rows: 5rem 1fr 5rem;
  grid-template-columns: 1fr;
  height: 100%;
}
a {
  text-decoration: none;
}
a:hover {
  color: gold;
}

header {
  grid-area: header;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  background-color: #f94856;
  color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  padding: 0.8rem;
}
header a {
  color: white;
  font-size: 30px;
  text-decoration: none;
}
main {
  grid-area: main;
}
footer {
  grid-area: footer;
  background-color: #f94856;
  color: #ffffff;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}

.content {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}
.main {
  -webkit-flex: 3 1 60rem;
          flex: 3 1 60rem;
}
.sidebar {
  -webkit-flex: 1 1 20rem;
          flex: 1 1 20rem;
}

/* products */
.products {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  padding: 0;
  margin: 0;
  list-style-type: none;
}
.products li {
  -webkit-flex: 0 1 29rem;
          flex: 0 1 29rem;
  height: 47rem;
  padding: 1rem;
}
.product {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  height: 100%;
}
.product img {
  max-width: 37rem;
  max-height: 37rem;
}
.product-price {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.product-price div,
.product-price button {
  text-align: center;
  -webkit-flex: 1 1;
          flex: 1 1;
}
.product-price div {
  font-size: 2.3rem;
}
.button {
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
  background-color: #e45959;
  cursor: pointer;
}
.button:hover {
  border: 0.1rem #808080 solid;
}
/* Filter */
.filter {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  padding: 1rem;
  margin: 1rem;
  border-bottom: 0.1rem #c0c0c0 solid;
}
.filter div {
  -webkit-flex: 1 1;
          flex: 1 1;
}
/* Index.css */
.cart {
  padding: 1rem;
  margin: 1rem;
  display: -webkit-flex;
  display: flex;
}
.cart-header {
  border-bottom: 0.1rem #c0c0c0 solid;
}
.cart-items {
  padding: 0;
  width: 100%;
  list-style-type: none;
}
.cart-items img {
  width: 5rem;
}
.cart-items li {
  display: -webkit-flex;
  display: flex;
}
.cart-items li div {
  padding: 0.5rem;
}
.cart-items li div:last-child {
  -webkit-flex: 1 1;
          flex: 1 1;
}
.right {
  text-align: right;
}
.total {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
  width: 100%;
}
.total > div {
  font-size: 2rem;
}
.total > div,
.total > button {
  -webkit-flex: 1 1;
          flex: 1 1;
}
/* Checkout Form */
form {
  width: 100%;
}
.promo{

}
.form-container {
  padding: 0;
  width: 100%;
  list-style-type: none;
}
.form-container li {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  padding: 1rem;
}
input {
  padding: 1rem;
  border: 0.1rem #c0c0c0 solid;
}
/* Product Details */
.product-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  max-height: 96vh;
}
.product-details img {
  max-height: 100vh;
  max-width: 46rem;
  margin: 0 auto;
}
.product-details-description {
  -webkit-flex: 1 1;
          flex: 1 1;
  margin: 1rem;
}
.close-modal {
  position: absolute;
  right: 1rem;
  top: 2.5rem;
  z-index: 1000;
}

.order-details {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  height: 100%;
  font-size: 30px;
}
.order-details li {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}
.order-details li div {
  padding: 10px;
}
.order-details .items {
  padding: 0;
  text-align: right;

}
.success-message {
  color: green;
}

